/* =============================================================================
   FOOTER
   ========================================================================== */
.page-footer {
  position: relative;
}
.page-footer .row {
  margin-bottom: 0;
}
@media (min-width: 47em) {
  .page-footer {
    height: 60px;
    margin: -60px 0 0;
    z-index: 5;
  }
}
a.esign {
  width: 104px;
  height: 20px;
  background-image: url('../images/logo-esign.png');
  text-indent: -9999px;
  opacity: 0.6;
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-transition: 1s cubic-bezier(0.25, 0.1, 0.25, 1);
  -moz-transition: 1s cubic-bezier(0.25, 0.1, 0.25, 1);
  -o-transition: 1s cubic-bezier(0.25, 0.1, 0.25, 1);
  transition: 1s cubic-bezier(0.25, 0.1, 0.25, 1);
  -webkit-transition: 1s cubic-bezier(0, 0.13, 0.64, 1);
  -moz-transition: 1s cubic-bezier(0, 0.13, 0.64, 1);
  -o-transition: 1s cubic-bezier(0, 0.13, 0.64, 1);
  transition: 1s cubic-bezier(0, 0.13, 0.64, 1);
  display: block;
  outline: 0;
}
@media all and (-webkit-min-device-pixel-ratio: 1.5) {
  a.esign {
    background-image: url('../images/logo-esign@2x.png');
    background-size: 104px 20px;
  }
}
a.esign:hover,
a.esign:focus {
  opacity: 1;
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  backface-visibility: hidden;
}
@media (min-width: 47em) {
  a.esign {
    float: right;
  }
}
/* =============================================================================
   CONTENT
   ========================================================================== */
.large {
  font-size: 16px;
  font-size: 1.6rem;
}
.center-block {
  text-align: center;
  *zoom: 1;
  margin: 20px 0;
}
.center-block:before,
.center-block:after {
  display: table;
  content: " ";
}
.center-block:after {
  clear: both;
}
.center-block .button {
  float: none;
  display: inline-block;
}
.box {
  margin: 0 0 20px;
  padding: 15px 15px 5px;
  *zoom: 1;
  background: #E7E4DB;
}
.box:before,
.box:after {
  display: table;
  content: " ";
}
.box:after {
  clear: both;
}
.box ul,
.box p {
  margin-bottom: 1em;
}
.box.error {
  background: #f1062c;
  color: #fff;
}
.box.success {
  background: #FF8000;
}
section {
  position: relative;
}
article {
  *zoom: 1;
  margin: 0 0 20px;
}
article:before,
article:after {
  display: table;
  content: " ";
}
article:after {
  clear: both;
}
article a img {
  display: block;
  -webkit-transition: 0.2s cubic-bezier(0.25, 0.1, 0.25, 1);
  -moz-transition: 0.2s cubic-bezier(0.25, 0.1, 0.25, 1);
  -o-transition: 0.2s cubic-bezier(0.25, 0.1, 0.25, 1);
  transition: 0.2s cubic-bezier(0.25, 0.1, 0.25, 1);
  -webkit-transition: 0.2s cubic-bezier(0, 0.13, 0.64, 1);
  -moz-transition: 0.2s cubic-bezier(0, 0.13, 0.64, 1);
  -o-transition: 0.2s cubic-bezier(0, 0.13, 0.64, 1);
  transition: 0.2s cubic-bezier(0, 0.13, 0.64, 1);
  opacity: 1;
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  backface-visibility: hidden;
}
article a:hover img,
article a:focus img {
  opacity: 0.8;
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  backface-visibility: hidden;
}
.map {
  width: 100%;
  height: 350px;
  background: #efefef;
  margin: 0 0 40px;
}
/* =============================================================================
   PAGES
   ========================================================================== */
/* =============================================================================
   HOOKS
   ========================================================================== */
.lt-ie9 .logo img {
  max-width: inherit;
}
.lt-ie8 {
  /* box model ie7 clear fix */
}
.lt-ie8 .row {
  float: left;
  width: 100%;
  clear: both;
}
/* =============================================================================
   PRINT
   Inlined to avoid required HTTP connection: h5bp.com/r
   ========================================================================== */
@media print {
  @page {
    margin: 2cm 0.5cm 1cm;
  }
  html {
    font-size: 10px;
  }
  * {
    background-color: transparent !important;
    color: #000 !important;
    box-shadow: none !important;
    text-shadow: none !important;
  }
  .ir {
    text-indent: 0;
    overflow: visible;
    direction: inherit;
  }
  a,
  a:visited {
    text-decoration: underline;
  }
  tr,
  img {
    page-break-inside: avoid;
  }
  img {
    max-width: 100% !important;
  }
  .page-footer,
  .main-nav {
    display: none;
  }
  .one-half {
    width: 50%;
  }
  .two-third {
    width: 66.66%;
  }
  .one-third {
    width: 33.33%;
  }
  .three-fourth {
    width: 75%;
  }
  .one-fourth {
    width: 25%;
  }
}
